
#workDetail .order-detail-container .el-form-item {
  margin-bottom: 5px;
}
#workDetail .order-detail-container .el-icon-document {
  color: #97a8be;
  font-size: 18px;
}
#workDetail #approveImg .el-upload--picture-card {
  float: left;
  box-sizing: border-box;
  width: 34px;
  height: 30px;
  border: none;
  border-radius: 6px;
  background-color: #fbfdff;
  cursor: pointer;
}
#workDetail #approveImg .el-upload-list--picture-card .el-upload-list__item {
  margin-top: 20px;
  margin-left: 20px;
}
#workDetail #approveImg .el-upload--picture-card .upload-img {
  float: left;
}
#workDetail .approve-reason img {
  display: inline-block;
  margin: 10px;
  margin-left: 0;
  width: 100px;
  height: 100px;
  cursor: pointer;
}
#workDetail .approve-article {
  margin: 0 auto;
  padding-top: 50px;
  width: 960px;
  border-top: 1px solid #eee;
  /* margin-left: 100px;
       margin-right: 370px; */
}
#workDetail .order-detail {
  padding: 15px;
  background: #fff;
  font-size: 14px;
  min-height: calc(100vh - 152px);
}
#workDetail .order-title {
  overflow: hidden;
  margin: 0;
  padding: 0;
  height: 82px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: normal;
  font-size: 27px;
  line-height: 82px;
}
#workDetail .order-box {
  margin-bottom: 24px;
  border: 1px solid #e6e9eb;
  background: #f9f9f9;
}
#workDetail .order-detail-header {
  padding: 22px 30px;
  height: 126px;
}
#workDetail .user-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}
#workDetail .user-info {
  position: relative;
  top: -30px;
  display: inline-block;
  margin-left: 25px;
  height: 80px;
}
#workDetail .user-section {
  position: relative;
  top: 8px;
  color: #888a95;
  font-size: 14px;
}
#workDetail .order-time {
  float: right;
  color: #888a95;
  font-size: 12px;
}
#workDetail .user-name {
  color: #2f313a;
  font-size: 20px;
}
#workDetail .order-detail-container {
  padding: 10px 30px;
}
#workDetail .approve-box {
  position: relative;
  clear: both;
  padding: 20px;
  background: #fff;
  font-size: 14px;
}
#workDetail .approve-result {
  position: absolute;
  top: 25px;
  right: 0;
  width: 92px;
  height: 72px;
  background-color: #fff;
  background-position: center;
  background-repeat: no-repeat;
}
#workDetail .approve-record {
  clear: both;
  height: 54px;
  border-bottom: 1px solid #e6e9eb;
  color: #2f313a;
  font-size: 16px;
  line-height: 54px;
}
#workDetail .approve-result-agree {
  background-image: url("./img/success.png");
}
#workDetail .approve-result-refuse {
  background-image: url("./img/refuse.png");
}
#workDetail .approve-record-list {
  margin-top: 30px;
  margin-bottom: 100px;
  border-left: 1px dashed #e6e9eb;
}
#workDetail .approve-record-list p {
  margin-bottom: 5px;
}
#workDetail .approve-record-list li {
  padding: 20px 50px;
  padding-bottom: 0;
}
#workDetail .approve-record-list li:before {
  position: relative;
  top: 16px;
  display: block;
  margin-left: -56px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  content: '';
}
#workDetail .approve-reason {
  color: #888a95;
  word-wrap: break-word;
  word-break: break-all;
  font-size: 12px;
}
#workDetail .approve-time {
  padding-left: 16px;
  color: #888a95;
  font-size: 12px;
}
#workDetail .approve-record-ing .approve-name {
  color: #fc5f45;
}
#workDetail .approve-record-end .approve-name {
  color: #38c695;
}
#workDetail .approve-record-ing:before {
  background: #fc5f45;
}
#workDetail .approve-record-end:before {
  background: #38c695;
}
#workDetail .deal-status {
  color: #888a95;
}
#workDetail .approve-btn {
  text-align: right;
}
#workDetail .operate-btn {
  margin-top: 50px;
  width: 120px;
  height: 32px;
}
#workDetail .upload-img {
  display: inline-block;
  margin-top: 26px;
  width: 34px;
  height: 30px;
  background: url("./img/photo-icon.png");
}
#workDetail .word-break {
  word-break: break-all;
}
#workDetail .confirm-btn {
  display: block;
  height: 42px;
  line-height: 42px;
  width: 170px;
  color: #fff;
  padding: 0;
  background: #01cd78;
  margin: 30px auto;
}



