
.side-menu {
  position: fixed;
  height: 100%;
  width: 230px;
  background: #fff;
  left: 0;
  top: 72px;
  z-index: 999;
}
#toggleMenuLabel {
  cursor: pointer;
  display: block;
  height: 35px;
  width: 16px;
  margin: 0 auto;
  background: url("./img/menu.png") center no-repeat;
}
#toggleMenu:checked + .wrap .side-menu {
  width: 64px;
}
#toggleMenu:checked + .wrap .side-menu .menu-list li a {
  padding-left: 13px;
}
#sideMenu .el-menu,
#sideMenu .el-submenu .el-menu {
  background: #fff;
}
#sideMenu .el-menu-item {
  padding: 0 !important;
}
#sideMenu .el-menu-item .el-tooltip {
    padding: 0 !important;
}
#sideMenu .el-menu-item,
#sideMenu .el-submenu__title {
  height: 50px;
  line-height: 50px;
}
#sideMenu .el-menu-item .el-icon-message,
  #sideMenu .el-submenu__title .el-icon-message {
    margin-left: -5px;
}
#sideMenu .el-submenu__icon-arrow {
  display: none;
}
#sideMenu .side-toggle {
  display: block;
  width: 100%;
  margin: 0 auto;
  height: 36px;
  line-height: 36px;
  text-align: center;
  color: #90929c;
  font-size: 22px;
  border-bottom: 1px solid #eee;
}
#sideMenu .menu-list li {
  font-size: 16px;
}
#sideMenu .menu-list li.el-menu-item:hover {
  background: #f2f2f2;
}
#sideMenu .menu-list li span {
  font-size: 14px;
}
#sideMenu .menu-list li a {
  display: block;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
  padding-left: 40px;
  padding-right: 20px;
  transition: all 180ms ease-in-out;
  color: #555;
  border-left: 4px solid #fff;
}
#sideMenu .menu-list li a.router-link-active {
  color: #01cd78;
  background: #f2f2f2;
  border-left-color: #01cd78;
  border-left: 4px solid #01cd78;
}
#sideMenu .el-icon-menu {
  font-size: 16px;
}

