
#attenSet .calendar-color {
  float: right;
  margin: 10px 0;
}
#attenSet .calendar-color li {
  float: left;
  height: 20px;
  line-height: 20px;
  position: relative;
  margin-right: 15px;
}
#attenSet .calendar-color li span {
  height: 12px;
  width: 20px;
  display: inline-block;
}
#attenSet #calendar {
  overflow: hidden;
  margin-bottom: 12px;
  width: 100%;
  border: 1px solid #e5e6e5;
  border-right: none;
  border-bottom: none;
  font-size: 14px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#attenSet .calendar-list {
  position: relative;
  float: left;
  width: 14.28%;
  height: 90px;
  border-right: 1px solid #e5e6e5;
  border-bottom: 1px solid #e5e6e5;
  background: rgba(238, 238, 238, 0.15);
  list-style: none;
  text-align: center;
  cursor: pointer;
}
#attenSet .first {
  color: #23d183;
  background: #e8fff6;
}
#attenSet .second {
  color: #fff1e1;
  background: #f8a64a;
}
#attenSet .third {
  color: #f7ebff;
  background: #c06dec;
}
#attenSet .calendar-list .date {
  position: absolute;
  top: 12px;
  left: 12px;
  font-weight: bold;
  font-size: 16px;
  font-family: cursive;
}
#attenSet .calendar-list.disabled {
  color: #dadddb;
  cursor: no-drop;
}
#attenSet .calendar-week {
  height: 60px;
  background: #fff;
  line-height: 60px;
}
#attenSet .tiny-setting-modal .el-dialog {
  width: 500px;
}
#attenSet .check-tips {
  position: relative;
  top: -48px;
  left: 300px;
  color: red;
}
#attenSet .table-list {
  margin-bottom: 30px;
}



