
.honour {
  height: 300px;
  background: #fff;
  text-align: center;
}
.honour-title {
  text-align: center;
  padding: 20px 0;
  font-size: 16px;
  margin-bottom: 0;
}
.honour ul {
  padding-left: 0;
  overflow: hidden;
  margin: 0 40px;
}
.honour ul li {
  list-style: none;
  float: left;
  height: 210px;
  width: 20%;
  position: relative;
  cursor: pointer;
}
.honour-auto {
  width: 162px;
  height: 210px;
  margin: 0 auto;
  border: 1px solid #e5e5e5;
  position: relative;
}
.honour-auto:hover::before {
  opacity: 1;
  transition: opacity 0.5s;
}
.honour-auto::before {
  content: "";
  height: 8px;
  width: 16px;
  z-index: 99999;
  background: url("./../img/coll-icon.png");
  position: absolute;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  top: 100px;
  right: -5px;
  opacity: 0;
  transition: opacity 0.3s;
}
.honour ul li .honour-synopsis {
  position: absolute;
  width: 218px;
  height: 210px;
  top: -1px;
  left: 160px;
  background-color: #fff;
  display: none;
  z-index: 100;
  border: 1px solid #e5e5e5;
  padding: 10px;
  text-align: left;
  overflow: auto;
  box-shadow: 3px 0 15px 0 #e6e6e6;
}
.honour ul li .honour-synopsis p {
  margin-bottom: 8px;
  line-height: 18px;
  color: #666;
  word-wrap: break-word;
}
.honour ul li:nth-child(5) .honour-synopsis {
  left: -200px;
  box-shadow: 3px 0 15px 0 #ffffff;
}
.honour ul li:nth-child(5) .honour-auto:before {
  -webkit-transform: rotate(-270deg);
          transform: rotate(-270deg);
  left: -5px;
}
.honour ul li .honour-synopsis label {
  color: #999;
  font-weight: normal;
}
.honour ul li img {
  height: 100px;
  width: 100px;
  background: #fff;
}
.honour-name {
  height: 36px;
  line-height: 36px;
  text-align: center;
  border-bottom: 1px solid #e5e5e5;
  font-size: 14px;
  color: #3e3e3e;
  margin: 0 10px;
  margin-bottom: 9px;
}
.honour ul li a {
  display: block;
  color: #979797;
  font-size: 12px;
}



