
#newsDetail .file-download {
  height: 30px;
  font-size: 16px;
  line-height: 30px;
}
#newsDetail .container {
  overflow: hidden;
  margin: 0 auto;
  padding-bottom: 20px;
  min-width: 1150px;
  max-width: 1440px;
  background: #fff;
}
#newsDetail .news-detail {
  float: left;
  margin-left: 102px;
  padding-top: 48px;
  width: 680px;
}
#newsDetail .news-title {
  margin-bottom: 20px;
  color: #000;
  font-size: 26px;
  /* margin-top: 30px; */
  line-height: 36px;
}
#newsDetail .news-trends {
  float: right;
  margin-right: 88px;
  width: 220px;
  height: 1000px;
}
#newsDetail .news-trends-name {
  margin: 30px 0;
  font-size: 16px;
}
#newsDetail .news-trends ul li {
  margin-bottom: 40px;
  font-size: 14px;
}
#newsDetail .news-trends ul li a {
  display: block;
  color: #000;
  font-size: 14px;
  opacity: 0.8;
}
#newsDetail .news-trends ul li a:hover {
  color: #01cd78;
}
#newsDetail .news-trends-time {
  color: #000;
  font-size: 12px;
  opacity: 0.6;
}
#newsDetail .fast-share {
  display: inline-block;
  float: left;
  width: 130px;
  height: 200px;
}
#newsDetail .news-interact {
  float: right;
  margin-right: 28px;
}
#newsDetail .article-icon {
  height: 24px;
}
#newsDetail .article-icon i {
  display: inline-block;
  margin-left: 42px;
  padding-left: 20px;
  width: 20px;
  height: 16px;
  background-position: left bottom;
  background-repeat: no-repeat;
  color: #a0a1aa;
  font-style: normal;
  font-size: 12px;
}
#newsDetail .article-icon i.read {
  background-image: url("./img/read.png");
}
#newsDetail .article-icon i.thumb {
  background-image: url("./img/thumb.png");
  margin-right: 32px;
}
#newsDetail .article-icon i.thumb.on {
  background-image: url("./img/thumb-on.png");
}
#newsDetail .article-icon i.comments {
  margin-right: 0;
  background-image: url("./img/comments.png");
  cursor: pointer;
  transition: opacity 0.2s ease;
  margin-left: 0;
}
#newsDetail .article-icon i.comments:hover {
  opacity: 0.8;
  transition: opacity 0.2s ease;
}
#newsDetail .news-content {
  float: left;
  width: 680px;
}
#newsDetail .file-box p {
  padding-left: 15px;
  height: 30px;
  background: #e6e6e6;
  line-height: 30px;
}
#newsDetail .file-box .file-list {
  padding: 10px;
  border: 2px solid #e6e6e6;
}
#newsDetail .file-box .file-list li {
    position: relative;
}
#newsDetail .file-box .file-list li .preview {
      position: absolute;
      top: 5px;
      right: 0;
}
#newsDetail .file-box a.file-download {
  height: 30px;
  color: #787878;
  font-size: 16px;
  line-height: 30px;
}
#newsDetail .file-box a.file-download:hover {
    color: #01cd78;
}
#newsDetail .news-content img {
  max-width: 100%;
}
#newsDetail .news-category {
  margin-bottom: 35px;
  color: #9e9e9e;
  font-size: 14px;
}
#newsDetail .news-category-tag {
  display: inline-block;
  padding: 0 2px;
  height: 22px;
  border: 1px solid #999999;
  font-size: 12px;
  line-height: 22px;
}
#newsDetail .new-author {
  padding-left: 14px;
}
#newsDetail .news-time {
  margin: 0 10px;
}
#newsDetail .fr-element.fr-view {
  text-align: justify;
  font-size: 16px;
  line-height: 30px;
}
#newsDetail #goTop {
  position: fixed;
  right: 10px;
  bottom: 100px;
  display: none;
  width: 54px;
  height: 54px;
  background: url("./img/go-top.png");
  cursor: pointer;
}
#newsDetail .el-loading-spinner {
  position: fixed;
  margin-left: -180px;
  width: 100px;
  left: 50%;
}
#newsDetail .el-loading-text {
  color: #01cd78;
}
#newsComments {
  clear: both;
}
#newsComments .to-thumb {
    position: relative;
    margin-top: 60px;
    margin-bottom: 40px;
    width: 180px;
    height: 56px;
    border: 1px solid #01cd78;
    border-radius: 40px;
}
#newsComments .to-thumb-icon {
    float: left;
    width: 50%;
    height: 100%;
    background-image: url("./img/thumb-icon.png");
    background-position: 44px center;
    background-repeat: no-repeat;
    cursor: pointer;
}
#newsComments .thumb-num-change {
    position: absolute;
    top: 19px;
    right: 25px;
    color: #01cd78;
    opacity: 0;
}
#newsComments .to-thumb-icon.on {
    background-image: url("./img/thumb-icon-on.png");
}
#newsComments .to-thumb-icon.on.animate {
    -webkit-animation: thumb 0.5s;
            animation: thumb 0.5s;
}
#newsComments .to-thumb-num {
    float: right;
    margin-top: 13px;
    padding-left: 22px;
    width: 50%;
    height: 30px;
    border-left: 1px solid #01cd78;
    color: #01cd78;
    font-size: 15px;
    line-height: 30px;
}
@-webkit-keyframes thumbChange {
0% {
    opacity: 1;
}
100% {
    top: 0;
    opacity: 0;
}
}
@keyframes thumbChange {
0% {
    opacity: 1;
}
100% {
    top: 0;
    opacity: 0;
}
}
@-webkit-keyframes thumb {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes thumb {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
#newsComments .c-area .emoji-box {
    top: 40px;
    left: -0;
    z-index: 10;
    overflow: hidden;
    width: 425px;
    height: 0px;
    background: white;
    box-shadow: 0 4px 20px 1px rgba(0, 0, 0, 0.2);
}
#newsComments .c-area .emoji-box.open {
    margin-top: -4px;
    margin-bottom: 20px;
    height: 250px;
    transition: height 0.2s;
}
#newsComments .c-area .emoji-box.open:after {
      opacity: 1;
}
#newsComments .c-area .emoji-box:after {
    position: absolute;
    top: 31px;
    left: 22px;
    width: 16px;
    height: 8px;
    background: url("./img/coll-icon.png");
    content: "";
    opacity: 0;
}
#newsComments .c-area .c-area-l {
    float: left;
    width: 48px;
    height: 48px;
}
#newsComments .c-area .c-area-l a {
      display: block;
      height: 100%;
}
#newsComments .c-area .c-area-l a img {
        width: 48px;
        height: 48px;
        border-radius: 100%;
}
#newsComments .c-area .c-area-r {
    float: right;
    width: 614px;
}
#newsComments .c-area .c-area-r .comments-box {
      position: relative;
}
#newsComments .c-area .c-area-r .comments-box textarea {
        margin-top: 4px;
        padding: 5px 7px;
        width: 613px;
        height: 40px;
        border: 1px solid #dddddd;
        color: #1f2d3d;
        font-size: 14px;
        line-height: 26px;
        resize: none;
        transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), height 0.2s;
}
#newsComments .c-area .c-area-r .comments-box textarea:focus {
          border: 1px solid #01cd78;
          transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
#newsComments .c-area .c-area-r .comments-box textarea:focus + button + .comments-bar {
            border: 1px solid #01cd78;
            border-top: 1px solid transparent;
            transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
#newsComments .c-area .c-area-r .comments-box .comments-submit {
        position: absolute;
        right: 0;
        bottom: 43px;
        z-index: 100;
        float: right;
        width: 90px;
        height: 40px;
        border: none;
        border-radius: 0;
        background: #01cd78;
        color: #fff;
        font-size: 14px;
        cursor: pointer;
}
@-moz-document url-prefix() {
#newsComments .c-area .c-area-r .comments-box .comments-submit {
    bottom: 41px;
}
}
#newsComments .c-area .c-area-r .comments-box .comments-bar {
        position: relative;
        top: -4px;
        z-index: 99;
        height: 40px;
        border: 1px solid #dddddd;
        background: #f5f5f7;
        opacity: 0;
        transition: all 0.3s;
}
#newsComments .c-area .c-area-r .comments-box .comments-bar .emoji-btn {
          position: relative;
          top: 0;
          left: 10px;
          display: inline-block;
          width: 40px;
          height: 40px;
          background: url("./img/smile-icon.png") no-repeat center;
          background-size: 50%;
          font-size: 20px;
          cursor: pointer;
}
#newsComments .c-area .c-area-r .comments-box .comments-bar .emoji-btn:hover {
            opacity: 0.8;
}
#newsComments .c-area .c-area-r .comments-box.focus textarea {
      height: 140px;
      transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), height 0.2s;
}
#newsComments .c-area .c-area-r .comments-box.focus .comments-submit {
      bottom: 4px;
}
#newsComments .c-area .c-area-r .comments-box.focus .comments-bar {
      opacity: 1;
}
#newsComments .c-area .comments-list .list-l {
    clear: both;
    overflow: hidden;
    margin-bottom: 38px;
}
#newsComments .c-area .comments-list .list-l .multi-reply {
      position: relative;
      top: 8px;
      clear: both;
      margin-top: 26px;
      margin-left: 64px;
      padding-top: 30px;
      padding-left: 18px;
      width: 615px;
      border-top: 1px solid #f0f2f1;
      background: #f5f6fa;
}
#newsComments .c-area .comments-list .list-l .multi-reply .list-l {
        margin-bottom: 0;
        padding-bottom: 26px;
}
#newsComments .c-area .comments-list .list-l .multi-reply .list-l .user-info em {
          padding-right: 15px;
}
#newsComments .c-area .comments-list .list-l .multi-reply .c-area-r {
        width: 522px;
}
#newsComments .c-area .comments-list .list-l .multi-reply .c-area-r .content {
          width: 480px;
}
#newsComments .c-area .comments-list .list-l .multi-reply .comments-box {
        width: 510px;
}
#newsComments .c-area .comments-list .list-l .multi-reply .comments-box textarea {
          width: 100%;
}
#newsComments .c-area .comments-list .list-l .multi-reply:before {
      position: absolute;
      top: -6px;
      left: 105px;
      border-right: 8px solid transparent;
      border-bottom: 5px solid #f4f5f9;
      border-left: 8px solid transparent;
      content: "";
}
#newsComments .c-area .comments-list .list-l .user-info {
      color: #999;
      font-size: 12px;
}
#newsComments .c-area .comments-list .list-l .user-info em {
        float: right;
        font-style: normal;
}
#newsComments .c-area .comments-list .list-l .user-info .user-name {
        padding-right: 8px;
        color: #38c695;
        font-size: 14px;
}
#newsComments .c-area .comments-list .list-l .content {
      margin-top: 8px;
      width: 610px;
      color: rgba(0, 0, 0, 0.8);
      word-break: break-all;
      font-size: 14px;
      line-height: 26px;
}
#newsComments .c-area .comments-list .list-l .article-icon {
      margin-top: 10px;
}
#newsComments .c-area .active-comments {
    -webkit-animation: animations 1.5s ease 2;
            animation: animations 1.5s ease 2;
    padding: 10px 0;
}
@-webkit-keyframes animations {
0% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0);
}
50% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0.8);
}
100% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0);
}
}
@keyframes animations {
0% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0);
}
50% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0.8);
}
100% {
    box-shadow: 0 0 10px rgba(158, 158, 158, 0);
}
}
#newsComments .comments-num {
    clear: both;
    margin-top: 64px;
    margin-bottom: 36px;
    padding-bottom: 14px;
    border-bottom: 1px solid #e5e9ec;
    font-size: 18px;
}
#newsComments .no-comments {
    margin: 0 auto;
    margin-bottom: 50px;
    padding-top: 140px;
    width: 226px;
    height: 92px;
    background: url("./img/no-comments.png") no-repeat center;
    background-size: contain;
    text-align: center;
}
#newsComments .no-comments a {
    color: #01cd78;
}



