.edui-container {
  position: relative;
  outline: 1px solid #d4d4d4;
  /* box-shadow: 2px 2px 5px #d3d6da; */
  background-color: #fff;
  width: 100% !important;
  overflow: hidden;
}

.edui-container .edui-toolbar {
  position: relative;
  width: auto;
  border-bottom: 1px solid #ccc;
  /* box-shadow: 2px 2px 5px #d3d6da;
    background-color: #fafafa; */
  z-index: 99999;
  background: #fff;
}

.edui-toolbar .edui-btn-toolbar {
  position: relative;
  padding: 0 5px;
}

.edui-container .edui-editor-body {
  background-color: #fff;
}
 .edui-body-container-editor{
   width: 100% !important;
   height: 800px !important;
 }
 .edui-body-container hr{
   margin: 8px 0
 }
 .editor-article-write container{
  max-width: 640px;
  margin: 0 auto;
 }
 chimee-center-state.loading{
   background: url('./../images/loading.svg') no-repeat center;
 }
chimee-progressbar-all, chimee-volume.horizonal chimee-volume-bar-all{
   background: #03cf7c !important;
 }
 .edui-body-container ul, .edui-body-container li, .edui-body-container ol{
   margin-left: 10px
 }
 .edui-body-container .fr-fic{
   max-width: 100%
 }
.edui-editor-body .edui-body-container {
  line-height: 1.8;
}

.edui-editor-body .edui-body-container p {
  margin: 5px 0;
}

.edui-editor-body .edui-body-container {
  border: 0;
  outline: none;
  cursor: text;
  padding: 0 10px 0;
  overflow: auto;
  display: block;
  word-wrap: break-word;
  font-size: 16px;
  font-family: sans-serif;
}

.edui-editor-body.focus {
  border: 1px solid #5c9dff;
}

.edui-editor-body table {
  margin: 10px 0 10px;
  border-collapse: collapse;
  display: table;
}

.edui-editor-body td {
  padding: 5px 10px;
  border: 1px solid #ddd;
}

.edui-editor-body iframe.mathquill-embedded-latex {
  border: 0px;
  padding: 0px;
  display: inline;
  margin: 0px;
  background: none;
  vertical-align: middle;
  width: 0px;
  height: 0px;
}
.umeditor_upload{
  height: 100px;
  position: relative;
  text-align: center;
  width: 300px;
  margin: 0 auto;
}
.umeditor_upload span{
  position: absolute;
  left: 50%;
  margin-left: -20px;
}
.umeditor_upload img{
  vertical-align: middle;
    height: 32px;
    width: 32px;
    margin-top: 34px;
}

.edui-editor-body .thumb-img{
  box-sizing:content-box;
}
