
#oaIndex {
  max-width: 1440px;
  min-width: 1150px;
  margin: 0 auto;
  padding-left: 0;
}
#oaIndex .content {
    margin-top: 96px;
    margin-bottom: 24px;
    overflow: hidden;
    padding: 0 24px;
    min-width: 1100px;
}
#oaIndex .content-side {
    width: 300px;
    float: right;
}
#oaIndex .index-content {
    margin-right: 324px;
}
#oaIndex .content-l {
    height: 400px;
    float: left;
}
#oaIndex .content-l .box {
    background: #fff;
    width: 280px;
    margin-bottom: 24px;
}
#oaIndex .weather {
    height: 100px;
    padding: 24px 0 24px 30px;
}
#oaIndex .weather ul {
    padding-left: 0;
}
#oaIndex .weather ul li {
    list-style: none;
    font-size: 16px;
    color: #3e3e3e;
    position: relative;
    height: 30px;
    line-height: 30px;
}
#oaIndex .weather ul li span {
    padding-left: 38px;
}
#oaIndex .weather ul li .icon {
    position: absolute;
    height: 16px;
    width: 18px;
    background-image: url("./img/icon.png");
    top: 7px;
}
#oaIndex .weather ul li .icon-calendar {
    background-position: -24px -136px;
}
#oaIndex .weather ul li .icon-address {
    background-position: -66px -136px;
}
#oaIndex .person-info {
    height: 196px;
    padding-top: 30px;
    text-align: center;
}
#oaIndex .person-info img {
    height: 72px;
    width: 72px;
    border-radius: 100%;
    margin: 0 auto;
    display: block;
    margin-bottom: 18px;
}
#oaIndex .person-name {
    font-size: 16px;
    color: #3e3e3e;
    margin-bottom: 16px;
}
#oaIndex .person-pos {
    text-align: center;
    color: #979797;
    font-size: 14px;
}
#oaIndex .person-pos span {
    padding-left: 15px;
}
#oaIndex .content-r {
    margin-left: 304px;
}
#oaIndex .slide-toggle {
    text-align: center;
}
#oaIndex .slide-toggle a {
    display: inline-block;
    height: 6px;
    width: 6px;
    background: #b9b9b9;
    border-radius: 100%;
    margin: 0 2px;
    cursor: pointer;
}
#oaIndex .slide-toggle a.active {
    width: 20px;
    background: #01cd78;
    border-radius: 5px;
}
#oaIndex .list-tag {
    position: relative;
}
#oaIndex .list-tag span {
    padding-left: 12px;
    font-size: 16px;
    color: #8e8e90;
}
#oaIndex .list-tag:before {
    position: absolute;
    top: 3px;
    content: '';
    height: 16px;
    width: 4px;
    background: #01cd78;
    border-radius: 3px;
    display: inline-block;
}
#oaIndex .list-tag a {
    color: #868f94;
    font-size: 14px;
    float: right;
}
#oaIndex .list-tag {
    position: relative;
}
#oaIndex .list-tag span {
    padding-left: 12px;
    font-size: 16px;
    color: #8e8e90;
}
#oaIndex .list-tag:before {
    position: absolute;
    top: 3px;
    content: '';
    height: 16px;
    width: 4px;
    background: #01cd78;
    border-radius: 3px;
    display: inline-block;
}
#oaIndex .list-tag a {
    color: #868f94;
    font-size: 14px;
    float: right;
}
#oaIndex .el-carousel__button {
    background-color: #01cd78;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background: #b9b9b9;
    opacity: 1;
}
#oaIndex .el-carousel__indicator.is-active button {
    width: 20px;
    background: #01cd78;
    border-radius: 5px;
}
#oaIndex .el-carousel__arrow:hover,
  #oaIndex .el-carousel__arrow {
    background: transparent;
}
#oaIndex .el-carousel__arrow i {
    font-size: 25px;
    color: #efefef;
    font-weight: bold;
}
#oaIndex .el-carousel__arrow:hover i {
    color: #01cd78;
}
#oaIndex .person-new .el-carousel__indicators {
    bottom: 20px;
}
#oaIndex .header-box {
    max-width: 1440px;
    padding: 0 24px;
}



