
#newsList .banner {
  background: url("./img/news-list-banner.png") no-repeat center;
  height: 300px;
}
#newsList .el-pagination {
  margin-top: 40px;
}
#newsList .news-content {
  background: #fff;
  min-height: 500px;
  padding: 30px 40px;
}
#newsList .news-box .el-loading-spinner {
  top: 200px;
}
#newsList .news-box .el-loading-spinner .el-loading-text {
  color: #01cd78;
}
#newsList .news-box li {
  height: 205px;
  padding-top: 45px;
  border-bottom: 1px solid #e5e9ec;
}
#newsList .news-box .news-img {
  width: 230px;
  height: 120px;
  float: left;
  margin-right: 16px;
}
#newsList .news-box .news-img img {
    width: 230px;
    height: 120px;
}
#newsList .news-box .read-more {
  color: #01cd78;
  position: absolute;
  right: 0;
  bottom: 3px;
  background: #fff;
  padding: 0 10px;
}
#newsList .news-box .news-detail {
  position: relative;
}
#newsList .news-box .news-detail .news-title {
    font-size: 20px;
    font-weight: normal;
    color: #37383a;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#newsList .news-box .news-detail .news-info {
    height: 26px;
    font-size: 12px;
    padding-top: 8px;
    color: #afafaf;
}
#newsList .news-box .news-detail .news-abstract {
    text-indent: 30px;
    font-size: 12px;
    color: #808080;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-top: 22px;
}
#newsList .news-box .news-detail .news-interact {
    float: right;
}
#newsList .news-box .news-detail .news-interact i {
      display: inline-block;
      width: 20px;
      height: 16px;
      margin-left: 32px;
      padding-left: 20px;
      font-style: normal;
      background-position: left bottom;
      background-repeat: no-repeat;
}
#newsList .news-box .news-detail .news-interact i.read {
      background-image: url("./img/read.png");
}
#newsList .news-box .news-detail .news-interact i.thumb {
      background-image: url("./img/thumb.png");
}
#newsList .news-box .news-detail .news-interact i.comments {
      background-image: url("./img/comments.png");
      margin-right: 26px;
}
#newsList .search-panel .el-input {
  width: 628px;
  z-index: 99;
  transition: width 0.2s ease;
}
#newsList .search-panel .el-input.focus {
  width: 790px;
  transition: width 0.3s ease;
}
#newsList .search-panel .el-icon-search {
  font-size: 20px;
  color: #a0a0aa;
  right: 12px;
  cursor: pointer;
}
#newsList .search-panel input {
  width: 100%;
  height: 46px;
  padding-left: 20px;
}
#newsList .search-panel .write-article {
  text-align: center;
  width: 142px;
  height: 46px;
  line-height: 46px;
  font-size: 16px;
  color: #fff;
  background: #01cd78;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transform-origin: right;
          transform-origin: right;
}
#newsList .search-panel .write-article.small {
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center;
          transform-origin: center;
}
#newsList .search-panel i.icon {
  display: inline-block;
  width: 22px;
  height: 22px;
  background-image: url("./img/icon.png");
  background-repeat: no-repeat;
  position: relative;
  top: 7px;
  padding-right: 38px;
}
#newsList i.icon.icon-all {
  background-position: -200px -31px;
}
#newsList .is-active i.icon.icon-all {
  background-position: -200px -6px;
}
#newsList i.icon.icon-news {
  background-position: -32px -33px;
}
#newsList .is-active i.icon.icon-news {
  background-position: -32px -6px;
}
#newsList i.icon.icon-notice {
  background-position: 0 -34px;
}
#newsList .is-active i.icon.icon-notice {
  background-position: 0 -8px;
}
#newsList i.icon.icon-share {
  background-position: -163px -34px;
}
#newsList .is-active i.icon.icon-share {
  background-position: -163px -8px;
}
#newsList i.icon.icon-rule {
  background-position: -130px -34px;
}
#newsList .is-active i.icon.icon-rule {
  background-position: -130px -8px;
}
#newsList .news-list-tab {
  width: 100%;
}
#newsList .news-list-tab .el-tabs__item {
  display: block;
  height: 48px;
  line-height: 48px;
  border-radius: 6px;
}
#newsList .news-list-tab .el-tabs__header {
  border-bottom: none;
  width: 280px;
  float: left;
}
#newsList .news-list-tab .el-tabs__content {
  margin-left: 310px;
}
#newsList .news-list-tab.el-tabs--card .el-tabs__header .el-tabs__item.is-active {
  border: none;
  color: #fff;
  background-color: #01cd78;
}
#newsList .news-list-tab .el-tabs__nav {
  width: 200px;
}



