
#newPerson .el-dialog--tiny {
  width: 600px;
}
#newPerson .batch-btns {
  width: 230px;
  float: left;
  margin-top: -58px;
  margin-left: 110px;
}
#newPerson .vue-image-crop-upload .vicp-preview {
  height: 180px;
  overflow: inherit;
}
#newPerson .vue-image-crop-upload .vicp-preview-item {
  width: 180px;
  height: 180px;
}
#newPerson .vue-image-crop-upload .vicp-preview-item img {
    width: 180px !important;
    height: 180px !important;
    border: 0;
}
#newPerson .vue-image-crop-upload .vicp-preview-item span {
    opacity: 0;
}
#newPerson .cover-pic {
  height: 135px;
  line-height: 135px;
  width: 135px;
  border: 1px dashed #c0ccda;
  border-radius: 6px;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
}
#newPerson .cover-pic .el-icon-plus {
    font-size: 28px;
    color: #8c939d;
}
#newPerson .cover-pic img {
    width: 100%;
    height: 100%;
}



