
.el-loading-spinner {
  background: #fff;
}
#attenReport .department {
  float: left;
  width: 300px;
}
#attenReport .panel {
  overflow: hidden;
}
#attenReport .calendar-modal .el-dialog {
  width: 1300px;
  height: 700px;
}
#attenReport .punch-time {
  text-align: left;
  padding: 2px;
  line-height: 25px;
  height: 25px;
}
#attenReport .punch-time .status {
    float: right;
    text-align: right;
}
#attenReport .el-table .cell {
  padding: 0 12px;
}
#attenReport tbody tr td:nth-child(2) .cell,
#attenReport tbody tr td:nth-child(3) .cell {
  visibility: visible;
}



