
#organizationDepart {
  width: 300px;
  float: left;
}
#organizationDepart .edit-tree i {
    padding: 0 8px;
    color: #8391a5;
    visibility: hidden;
}
#organizationDepart .edit-tree {
    float: right;
}
#organizationDepart .el-tree {
    height: 550px;
    overflow: auto;
    margin-bottom: 100px;
}
#organizationDepart .el-tree-node__content:hover .edit-tree i {
    visibility: visible;
}
#organizationDepart .el-tree-node__content .edit-tree i:hover {
    color: #01cd78;
}



