
.editor_modal_image .el-dialog {
  width: 600px;
}
.editor_modal_image .el-dialog .el-upload-list {
    max-height: 350px;
    overflow-y: scroll;
}
.editor_modal_image .el-dialog .el-upload-list__item {
    width: 170px;
    height: 160px;
    float: left;
    margin-right: 10px;
}
.editor_modal_image .el-dialog .el-upload-list__item img {
      height: 130px;
      width: 150px;
      margin-top: 10px;
}
.editor_modal_image .el-dialog .el-upload-list__item .el-upload-list__item-name i {
      display: none;
}
.editor_modal_image .el-dialog .el-upload-list--picture .el-upload-list__item {
    padding: 0;
}
.editor_modal_image .el-dialog .el-upload-list__item-thumbnail {
    margin-left: 0;
}
.editor_modal_image .el-dialog .el-upload-list__item:nth-child(3n) {
    margin-left: 0 !important;
}
.el-upload-dragger {
  width: 560px;
}



