
#articlePage .fr-element.fr-view {
  font-size: 16px;
  line-height: 30px;
  text-align: justify;
  padding-bottom: 40px;
}
#articlePage .fr-box.fr-basic.fr-element {
  padding: 10px 30px;
}
#articlePage .fr-element.fr-view p {
  line-height: 1.8;
}
#articlePage .cover-picture {
  height: 160px;
  margin-top: -10px;
}
#articlePage .cover-picture .el-form-item__content {
  top: 36px;
  left: -50px;
}
#articlePage .cover-pic {
  height: 120px;
  line-height: 120px;
  width: 230px;
  border: 1px dashed #01cd78;
  border-radius: 6px;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}
#articlePage .cover-pic:hover:before {
    opacity: 1;
}
#articlePage .cover-pic:hover .el-icon-delete2 {
    opacity: 1;
}
#articlePage .cover-pic:before {
    content: "";
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s;
    opacity: 0;
}
#articlePage .cover-pic img {
    left: 0;
    top: 0;
}
#articlePage .cover-pic .el-icon-delete2 {
    position: absolute;
    left: 100px;
    top: 50px;
    font-size: 21px;
    color: #fff;
    z-index: 88;
    opacity: 0;
}
#articlePage .cover-pic .icon-plus {
    background: url("./img/plus.png") no-repeat center;
    width: 90px;
    height: 30px;
    margin: 0 auto;
    margin-top: 26px;
    color: #01cd78;
}
#articlePage .article-content {
  background: #fff;
  padding: 48px 32px;
  padding-bottom: 80px;
}
#articlePage .article-content .newsTitle input {
    height: 48px;
    border: 0;
    border-radius: 3px 3px 0 0;
    outline: 1px solid #dddee1;
    outline-offset: 0px;
}
#articlePage .article-content .count-str {
    position: absolute;
    right: 15px;
    top: 6px;
    color: #40474d;
    font-size: 12px;
}
#articlePage .article-content .el-form-item {
    margin-bottom: 30px;
}
#articlePage .article-footer {
  height: 58px;
  width: 100%;
  background: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99999;
  box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.66);
}
#articlePage .article-footer .footer-btn {
    width: 1180px;
    margin: 0 auto;
    margin-top: 15px;
    text-align: right;
    padding-right: 32px;
}
#articlePage .article-footer .footer-btn .el-button {
      padding: 0;
      width: 100px;
      text-align: center;
      height: 30px;
      line-height: 30px;
      margin-left: 15px;
}
#articlePage .article-footer .footer-btn .el-button--info {
      border: 1px solid #e6eaed;
      color: #2d3039;
}



