
#personnelModal .el-dialog {
  width: 1000px;
}
#personnelModal .avatar {
  height: 150px;
  width: 150px;
  margin-left: 40px;
  border-radius: 50%;
  display: block;
  border: 1px solid #ededed;
}
#personnelModal .avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
#personnelModal .avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
#personnelModal .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 150px;
  height: 150px;
  line-height: 150px;
  margin-left: 40px;
  border: 1px dashed #dddee1;
  text-align: center;
}



