/**
 * element
 */

a {
    color: #40474d;
}

a:hover {
    color: #01cd78;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

html {
    /* background: #eef0f5; */
    font: 12px Microsoft YaHei, tahoma, arial, Hiragino Sans GB, \\5b8b\4f53, sans-serif;
}


/**/

.el-input__inner:focus,
.el-input__inner:hover {
    border-color: #01cd78;
}

.el-button--success {
    background-color: #01cd78;
    border-color: #01cd78;
}

.el-button--info,
.el-button--info:hover,
.el-button--info:focus,
.el-button--info:active {
    border-color: #01cd78;
    background-color: transparent;
    color: #01cd78;
}

.el-upload-list__item.is-success .el-upload-list__item-name:hover {
    color: #01cd78;
}


/**/

.el-breadcrumb {
    height: 80px;
    line-height: 80px;
    /* padding-left: 20px; */
}

.el-breadcrumb__item__inner a:hover,
.el-breadcrumb__item__inner:hover {
    color: #01cd78;
}


/**/

.el-input__inner {
    height: 32px;
    border: 1px solid #dddee1;
}

.el-input__inner:hover,
.el-select .el-input__inner:focus,
.el-select:hover .el-input__inner {
    border: 1px solid #01cd78;
}

.el-select-dropdown__item.selected.hover,
.el-select-dropdown__item.selected {
    background: #01cd78;
}

.search-panel .el-input {
    width: 160px;
}

.el-button {
    padding: 8px 12px;
}


/**/

.el-table th,
.el-table__header-wrapper thead div {
    background-color: #f2f2f2;
    font-weight: normal;
}

.el-table th>.cell,
.el-table .cell {
    font-size: 12px;
}

.el-table--border td,
.el-table--border th {
    border-right: 1px solid #ddd;
}

.el-table td,
.el-table th.is-leaf {
    border-bottom: 1px solid #ddd;
}

.el-table--enable-row-hover .el-table__body tr:hover>td {
    background-color: #f2f2f2;
}


/**/

.el-pager li.active,
.el-switch.is-checked .el-switch__core {
    border-color: #01cd78;
    background-color: #01cd78;
}

.el-pager li.active:hover {
    color: #fff;
}

.el-pagination button.disabled:hover {
    color: #e4e4e4;
}

.el-pager li:hover,
.el-pagination button:hover,
.el-time-panel__btn.confirm {
    color: #01cd78;
}

.el-pagination__editor {
    position: relative;
    top: -3px;
}

.el-dialog__title {
    font-size: 17px;
    font-weight: normal;
}

.el-dialog__header {
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 15px;
    margin: 0 30px;
    padding-left: 0;
}


/**/

.el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
    background: #01cd78;
}

.el-month-table td.current:not(.disabled) .cell,
.el-year-table td.current:not(.disabled) .cell,
.time-select-item.selected:not(.disabled),
.el-cascader-menu__item.is-active {
    background: #01cd78 !important;
}

.el-date-picker__header-label.active,
.el-date-picker__header-label:hover,
.el-picker-panel__icon-btn:hover,
.el-date-table td.today {
    color: #01cd78;
}


/**/

.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner,
.el-button--primary,
.el-button--primary:hover,
.el-radio__input.is-checked .el-radio__inner {
    background-color: #01cd78;
    border-color: #01cd78;
}

.el-checkbox__inner:hover,
.el-checkbox__inner.is-focus .el-checkbox__inner,
.el-textarea__inner:focus,
.el-radio__inner:hover {
    border-color: #01cd78;
}

.el-tag--primary,
.el-button--primary:focus,
.el-button--primary:hover {
    background-color: #01cd78;
    border-color: #01cd78;
    color: #fff;
}

.el-tag--primary .el-tag__close:hover {
    background-color: #01cd78;
    color: #fff;
    opacity: 0.8;
}

.el-select-dropdown.is-multiple .el-select-dropdown__item.selected {
    color: #01cd78;
}

.el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
    background: #01cd78 !important;
}

.el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
    color: #fff !important;
}

.el-date-table td.today:before {
    border-top: 0.5em solid #01cd78;
}

.el-table__fixed,
.el-table__fixed-right {
    height: 100% !important;
    box-shadow: 1px 0 8px rgba(0, 0, 0, 0);
}

.el-table__fixed-header-wrapper thead div {
    background-color: #f2f2f2;
}

.common-confirm {
    width: 300px;
}

.common-confirm .el-message-box__content {
    padding: 40px 20px;
}

.common-confirm .el-button {
    padding: 0;
    height: 42px;
    width: 120px;
}

.common-confirm .el-message-box__header {
    height: 40px;
    line-height: 40px;
    padding: 0;
    padding-left: 20px;
    border-bottom: 1px solid #eee;
}

.el-tag--primary.is-hit {
    border-color: #01cd78;
}

.common-confirm .el-message-box__headerbtn {
    top: 12px;
}

.el-tabs__item.is-active {
    color: #05d181;
}

.el-form-item.is-error .el-input__inner,
.el-form-item.is-error .el-textarea__inner {
    border: 1px solid #dddee1;
}


/* modal */

.tiny-modal .el-dialog {
    width: 550px;
}

.el-time-panel__content::after,
.el-time-panel__content::before {
    background-color: #01cd78;
}

.el-scrollbar__wrap {
    box-sizing: content-box;
}

.el-loading-spinner .path {
    stroke: #04d07f;
}



