
.project-manager .nav-header .header-box {
  min-width: 100% !important;
  padding: 0 24px;
}
.project-manager .el-dialog {
  z-index: 2001;
}
.project-manager .el-dialog .el-dialog__header {
    background-color: #eee;
    margin: 0;
    padding: 20px;
    border-bottom: 0 none;
}
.project-manager .el-dialog .el-dialog__header .el-icon-close {
      color: #aaa;
}
.project-manager .main-view {
  overflow-y: auto;
  margin-left: 230px;
  padding-left: 20px;
}
.project-manager .main-view .el-button.el-button--mini {
    color: #01cd78;
}
.project-manager .main-view .el-button.el-button--primary {
    color: #01cd78;
    border: 1px solid #01cd78;
    background-color: #fff;
}
.project-manager .main-view .el-button.el-button--primary:hover {
      color: #fff;
      border: 1px solid #01cd78;
      background-color: #01cd78;
}
.project-manager .main-view .el-button.el-button--mini {
    padding: 5px 3px;
}



