
#articleApprove .fr-element.fr-view {
  text-align: justify;
  word-break: break-all;
  font-size: 16px;
  line-height: 30px;
}
#articleApprove .fr-element.fr-view p {
  line-height: 1.8;
}
#articleApprove .file-box p {
  padding-left: 15px;
  height: 30px;
  background: #e6e6e6;
  line-height: 30px;
}
#articleApprove .file-box .file-list {
  padding: 10px;
  border: 2px solid #e6e6e6;
}
#articleApprove .file-box .file-list li {
    position: relative;
}
#articleApprove .file-box .file-list li .preview {
      position: absolute;
      top: 5px;
      right: 0;
}
#articleApprove .file-box a.file-download {
  height: 30px;
  color: #787878;
  font-size: 16px;
  line-height: 30px;
}
#articleApprove .file-box a.file-download:hover {
    color: #01cd78;
}
#articleApprove .article-content {
  padding-top: 40px;
  background: #fff;
}
#articleApprove .article-content .news-body {
    margin: 0 auto;
    padding-bottom: 40px;
    width: 680px;
}
#articleApprove .article-content .news-body .news-category {
      margin-top: 18px;
      margin-bottom: 35px;
      color: #9e9e9e;
      font-size: 14px;
}
#articleApprove .article-content .news-body .new-author {
      padding-left: 14px;
}
#articleApprove .article-content .news-body .news-category-tag {
      padding: 1px 2px;
      border: 1px solid #999999;
}
#articleApprove .article-content .news-body .news-time {
      margin: 0 10px;
}
#articleApprove .article-content .news-title {
    color: #000;
    text-align: left;
    font-weight: normal;
    font-size: 28px;
}



