.header[data-v-39cce19f] {
  height: 72px;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  box-shadow: 0 0 8px rgba(46, 48, 55, 0.51);
}
.header-box[data-v-39cce19f] {
  max-width: 1180px;
  margin: 0 auto;
  height: 72px;
  /* padding: 0 24px; */
}
.logo[data-v-39cce19f] {
  display: block;
  width: 204px;
  height: 72px;
  line-height: 72px;
  float: left;
  text-align: center;
}
.logo img[data-v-39cce19f] {
  vertical-align: middle;
}

