.search input[data-v-39cce19f] {
  width: 240px;
  border: none;
  outline: none;
  height: 28px;
  margin: 4px;
  margin-left: 40px;
  font-size: 14px;
  background: #f3f5f7;
}
.setting[data-v-39cce19f] {
  float: right;
  height: 72px;
  line-height: 72px;
}
.setting ul li.menu[data-v-39cce19f] {
  float: left;
  width: 70px;
  text-align: center;
  list-style: none;
  position: relative;
}
.setting ul li.menu a[data-v-39cce19f] {
  height: 72px;
  background-repeat: no-repeat;
  background-position: center;
}
.setting .menu a.message[data-v-39cce19f] {
  background-image: url('./img/message-icon.png');
}
.setting .menu a.setting[data-v-39cce19f] {
  background-image: url('./img/setting-icon.png');
}
.setting .menu a.log-out[data-v-39cce19f] {
  background-image: url('./img/logout-icon.png');
}
.setting ul li:hover .setting-icon[data-v-39cce19f] {
  transform: rotateX(-180deg);
  -webkit-transform: rotateX(-180deg);
  -moz-transform: rotateX(-180deg);
  top: -2px;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
}
.setting ul li.menu a[data-v-39cce19f] {
  color: #868f94;
  display: inline-block;
  width: 100%;
}
.setting ul li a[data-v-39cce19f]:hover {
  text-decoration: none;
}
.setting .sub-menu[data-v-39cce19f] {
  list-style: none;
  position: absolute;
  padding: 0;
  background: #fff;
  width: 120px;
  left: -20px;
  height: 120px;
  border-radius: 0 0 6px 6px;
  box-shadow: 0 0 10px #d4d4d4;
  top: 70px;
}
.setting .sub-menu li[data-v-39cce19f] {
  height: 60px;
  line-height: 60px;
}
.setting .sub-menu li[data-v-39cce19f]:hover {
  opacity: 0.8;
}
.email-num[data-v-39cce19f] {
  position: absolute;
  top: 20px;
  left: 35px;
  height: 15px;
  width: 22px;
  line-height: 14px;
  background: #fb6c2e;
  border-radius: 14px;
  color: #fff;
}
.email-num span[data-v-39cce19f] {
  position: relative;
  left: -3px;
}
.email-num em[data-v-39cce19f] {
  font-style: normal;
  position: relative;
  top: -16px;
  left: 7px;
}
@media screen and (max-width: 1366px) {
.header-box[data-v-39cce19f] {
    padding: 0 24px;
    max-width: 100%;
}
}
