
.my-work {
  width: 100%;
  height: 390px;
  background: #fff;
  padding: 20px 0 30px 0;
  margin-bottom: 24px;
}
.work-link {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}
.my-work .list-tag {
  padding: 0 30px 0 24px;
  margin-bottom: 15px;
}
#orderList .el-tabs__active-bar {
  background-color: #01cd78;
  height: 2px;
}
#orderList .el-tabs__item.is-active {
  color: #1f2d3d;
}
#orderList .el-tabs__item {
  padding: 0 24px;
}
#orderList .tab-content ul {
  padding: 0 24px;
  margin-top: 10px;
}
#orderList .tab-content ul li {
  list-style: none;
  height: 38px;
  font-size: 14px;
}
#orderList .work-list-info {
  float: right;
  color: #40474d;
}
#orderList .work-list-info em {
  font-style: normal;
  font-size: 13px;
}
#orderList .work-list-info .work-time {
  padding-left: 35px;
  color: #8d8d8d;
}



