
.content-box {
  margin-top: 72px;
}
#personInfo .no-data {
  text-align: center;
  height: 160px;
  width: 190px;
  margin: 40px auto;
  background: url("./img/invoice.png") no-repeat center;
}
#personInfo .person-info {
  height: 285px;
  padding-left: 40px;
  padding-top: 40px;
  background: #fff;
  margin-bottom: 20px;
}
#personInfo .avatar {
  float: left;
  width: 195px;
  height: 202px;
  text-align: center;
}
#personInfo .person-avatar {
  display: block;
  width: 150px;
  height: 150px;
  margin: 0 auto;
  cursor: default;
  position: relative;
}
#personInfo .person-avatar img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
}
#personInfo .person-avatar .upload-demo {
    position: absolute;
    bottom: -10px;
    left: 25px;
}
#personInfo .person-avatar .upload-demo .el-button {
      width: 100px;
}
#personInfo .person-day {
  margin-top: 25px;
  font-size: 16px;
}
#personInfo .person-day span {
  color: #f37f39;
  font-weight: bold;
}
#personInfo .datum {
  margin-left: 40px;
  float: left;
  height: 200px;
}
#personInfo .datum .name {
    font-size: 24px;
    color: #3e3e3e;
    margin-bottom: 24px;
}
#personInfo .datum .sex {
    display: inline-block;
    height: 19px;
    width: 19px;
    background: url("./img/icon.png") no-repeat center;
    margin-left: 2px;
    margin-right: 20px;
    position: relative;
    top: 4px;
}
#personInfo .datum .sex.boy {
    background-position: 0 -159px;
}
#personInfo .datum .sex.girl {
    background-position: 0 -127px;
}
#personInfo .datum .post {
    font-size: 15px;
    color: #999;
}
#personInfo .datum .list li {
    background: url("./img/icon.png") no-repeat center;
    height: 20px;
    line-height: 20px;
    font-size: 13px;
    color: #999;
    padding-left: 30px;
    margin-bottom: 20px;
}
#personInfo .datum .list li.depName {
    background-position: 2px 4px;
}
#personInfo .datum .list li.phone {
    background-position: 2px -29px;
}
#personInfo .datum .list li.email {
    background-position: 0 -54px;
}
#personInfo .datum .list li.integral {
    background-position: 0 -88px;
}
#personInfo .datum .list li.integral strong {
      color: #f07f3d;
}
#personInfo .datum .operate-light {
    color: #4177ff;
    margin-left: 60px;
    margin-right: 10px;
}
#personInfo .datum .person-info-phone {
    width: 180px;
    display: inline-block;
    margin-top: -4px;
}
#personInfo .footmark {
  background: #fff;
  min-height: calc(100vh - 457px);
}
#personInfo .footmark .el-tabs__item {
    padding: 0 40px;
    height: 57px;
    line-height: 57px;
}
#personInfo .footmark .el-tabs__active-bar {
    background-color: #01cd78;
}
#personInfo .footmark .el-tabs__item.is-active {
    color: #333333;
}
#personInfo .footmark .tag {
    padding: 0 2px;
    line-height: 18px;
    border-radius: 2px;
    display: inline-block;
    text-align: center;
    margin-right: 6px;
    font-size: 12px;
    border: 1px solid #fab33c;
    position: relative;
    top: -2px;
    font-weight: normal;
}
#personInfo .footmark .news-title {
    font-size: 20px;
    font-weight: normal;
    color: #37383a;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#personInfo .footmark .news-abstract {
    font-size: 12px;
    color: #808080;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-top: 22px;
    word-break: break-all;
}
#personInfo .footmark .news-box {
    padding: 0 30px;
}
#personInfo .footmark .article-list {
    padding: 0 30px;
}
#personInfo .footmark .article-list li {
      height: 115px;
      padding-top: 30px;
      border-bottom: 1px solid #e8e8e8;
}
#personInfo .footmark .article-list li .news-info {
        height: 26px;
        font-size: 12px;
        padding-top: 8px;
        color: #afafaf;
}
#personInfo .footmark .article-list li .news-interact {
        float: right;
}
#personInfo .footmark .article-list li .news-interact i {
          display: inline-block;
          width: 20px;
          height: 16px;
          margin-left: 32px;
          padding-left: 20px;
          font-style: normal;
          background-position: left bottom;
          background-repeat: no-repeat;
}
#personInfo .footmark .article-list li .news-interact i.read {
          background-image: url("./img/read.png");
}
#personInfo .footmark .article-list li .news-interact i.thumb {
          background-image: url("./img/thumb.png");
}
#personInfo .footmark .article-list li .news-interact i.comments {
          background-image: url("./img/comments.png");
          margin-right: 5px;
}
#personInfo .footmark .news-box {
    padding: 0 30px;
}
#personInfo .footmark .news-box li {
      min-height: 180px;
      padding-top: 30px;
      border-bottom: 1px solid #e5e9ec;
}
#personInfo .footmark .news-box .news-img {
      width: 230px;
      height: 120px;
      float: left;
      margin-right: 16px;
}
#personInfo .footmark .news-box .news-img img {
        width: 230px;
        height: 120px;
}
#personInfo .footmark .news-box .read-more {
      color: #01cd78;
      position: absolute;
      right: 0;
      bottom: 3px;
      background: #fff;
      padding: 0 10px;
}
#personInfo .footmark .news-box .news-detail {
      position: relative;
}
#personInfo .footmark .news-box .news-detail .news-info {
        height: 26px;
        font-size: 12px;
        padding-top: 8px;
        color: #afafaf;
}
#personInfo .footmark .el-pagination {
    float: right;
    margin: 40px 25px;
}



