
#companyDetails .el-dialog__body {
  padding: 20px 30px;
}
#companyDetails .el-dialog {
  width: 800px;
}
#companyDetails .el-dialog .el-form-item {
    margin-bottom: 10px;
}
#companyDetails .el-dialog .title {
    font-size: 16px;
}
#companyDetails .el-dialog .username {
    padding-right: 40px;
}
#companyDetails .el-dialog .time {
    float: right;
    padding-right: 10px;
}
#companyDetails .el-dialog .remark {
    overflow: hidden;
    width: 490px;
    display: inline-block;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#companyDetails .el-dialog .content {
    word-wrap: break-word;
    font-size: 14px;
}
#companyDetails .el-dialog .record-list li {
    height: 40px;
}
#companyDetails .el-dialog hr {
    margin-top: 5px;
}



