
#articlePrview .fr-element.fr-view {
  text-align: justify;
  word-break: break-all;
  font-size: 16px;
}
#articlePrview .file-download {
  display: block;
  height: 30px;
  font-size: 16px;
  line-height: 30px;
}
#articlePrview .new-author {
  padding-left: 14px;
}
#articlePrview .fr-element.fr-view p {
  line-height: 1.8;
}
#articlePrview .file-box p {
  padding-left: 15px;
  height: 30px;
  background: #e6e6e6;
  line-height: 30px;
}
#articlePrview .file-box .file-list {
  padding: 10px;
  border: 2px solid #e6e6e6;
}
#articlePrview .file-box .file-list li {
    position: relative;
}
#articlePrview .file-box .file-list li .preview {
      position: absolute;
      top: 5px;
      right: 0;
}
#articlePrview .file-box a.file-download {
  height: 30px;
  color: #787878;
  font-size: 16px;
  line-height: 30px;
}
#articlePrview .file-box a.file-download:hover {
    color: #01cd78;
}
#articlePrview .article-content {
  padding-top: 40px;
  background: #fff;
}
#articlePrview .article-content .news-body {
    margin: 0 auto;
    padding-bottom: 40px;
    min-height: 780px;
    width: 680px;
}
#articlePrview .article-content .news-body .news-category {
      margin-top: 18px;
      margin-bottom: 35px;
      color: #9e9e9e;
      font-size: 14px;
}
#articlePrview .article-content .news-body .news-category-tag {
      padding: 1px 2px;
      border: 1px solid #999999;
}
#articlePrview .article-content .news-body .news-time {
      margin: 0 10px;
}
#articlePrview .article-content .news-title {
    margin-bottom: 20px;
    color: #000;
    font-size: 26px;
    line-height: 36px;
}
#articlePrview .article-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99999;
  width: 100%;
  height: 58px;
  background: #fff;
  box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.66);
}
#articlePrview .article-footer .footer-btn {
    margin: 0 auto;
    margin-top: 15px;
    padding-right: 32px;
    width: 1180px;
    text-align: center;
}
#articlePrview .article-footer .footer-btn .el-button {
      margin-left: 15px;
      padding: 0;
      width: 100px;
      height: 30px;
      text-align: center;
      line-height: 30px;
}
#articlePrview .article-footer .footer-btn .el-button--info {
      border: 1px solid #e6eaed;
      color: #2d3039;
}



